  right: 0px;
  top: 0px;
  cursor: pointer;
}

.menu_bar .menu_bar_search div:hover {
  border-left: 3px solid #d6d6d6;
  border-radius: 50%;
}

.menu_bar .menu_bar_search_input {
  width: 350px;
  height: 35px;
  background-color: #eee;
  color: #252424;
  border-radius: 30px;
